@use '../../foundation/_variable' as *;

.p-campaign {
    background-color: $color-bg-cream;
    &__title {
        width: 28rem;
        text-align: center;
        margin: 0 auto;
        padding-top: 4.2rem;
        &Jp {
            border-bottom: 1px solid $color-font;
            font-weight: bold;
            font-family: $font-zenKaku;
        }
        &En {
            text-transform: uppercase;
            font-family: $font-din;
        }
    }
    &__heading {
        margin: 2.8rem auto 0;
        text-align: center;
        color: $color-base;
        font-weight: bold;
        font-size: 3.6rem;
        width: 46.6rem;
        height: 11rem;
        background: url("../../assets/img/campaign-heading-bg@2x-8.png");
    }
    &__catch {
        margin: 2.8rem auto 0;
        width: 46.6rem;
        height: 6rem;
    }
    &__content {
        margin-top: 3.8rem;
        text-align: center;
        font-size: 3rem;
        font-weight: bold;
    }
    &__chart {
        margin: 2rem auto 0;
    }
    &__note {
        font-size: 1.6rem;
        margin-top: 2rem;
        color: $color-black;
        font-weight: normal;
        text-align: left;
        :last-child {
            margin-top: 2.4rem;
        }
    }
    &__point {
        margin-top: 2rem;
        img {
            margin: 0 auto;
        }
    }



}
