@use '../../foundation/_variable' as *;

.c-btn {
    display: block;
    width: 52rem;
    height: 12rem;
    font-size: 3.6rem;
    font-weight: bold;
    color: $color-base;
    background: $color-green;
    padding: 1rem;
    border-radius: 6rem;
    box-shadow: 0px 3px 6px $color-gray;
    text-align: center;
    margin: 0 auto;
}
