@use '../../foundation/_variable' as *;

.p-cta {
    padding-top: 3rem;
    background: linear-gradient($color-bg-cream, transparent 20%);
    &__bgBase {
        background: $color-base;
    }
    &__bgYellow {
        background: linear-gradient($color-bg-yellow,  transparent 20%);
    }
    &__heading img {
        margin: 0 auto;
        width: 30rem;
        height: 5.6rem;
    }
    &__btn {
        margin: 2rem auto;
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: $font-zenKaku ;
        &::after {
            content: "";
            display: block;
            width: 3.4rem;
            height: 2.8rem;
            background: url("../../assets/img/btn-arrow.svg") center / contain no-repeat;
            margin-left: 2.4rem;
        }
    }
}
