@use '../foundation/_variable' as *;

.l-footer {
    background-color: $color-base;
    &__content {
        display: flex;
        padding: 0 2rem;
        gap: 2rem;
        align-items: flex-end;
        justify-content: space-between;
    }
    &__logo img {
        min-width: 11.5rem;
        height: auto;
        margin-bottom: 1.4rem;
    }
    &__img img {
        min-width: 28.5rem;
        height: auto;
    }
    &__nav ul {
        width: 28rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: start;
        margin-bottom: 2rem;
        font-size: 1.2rem;
        li {
            text-align: left;
        }
    }
    small {
        background-color: $color-green;
        font-weight: normal;
        font-size: 1.3rem;
        color: $color-base;
        text-align: center;
        height: 4rem;
        display: block;
        line-height: 4rem;
    }
}